    // date1是当前日期
    // date2是当年第一天
    // d是当前日期是今年第多少天
    // 用d + 当前年的第一天的周差距的和在除以7就是本年第几周
    const date1 = date,
        date2 = new Date(date.getFullYear(), 0, 1),
        d = Math.round((date1.valueOf() - date2.valueOf()) / 86400000);
    return Math.ceil((d + date2.getDay()) / 7);
}

function getDate(offset: Date, week: number, day: number) {
    const d = offset.valueOf() / 86400000,
        start = d - offset.getDay() + 7 * week;
    return new Date((start + day) * 86400000);
}

function now_week() {
    const now = new Date(),
        start_week = getYearWeek(start),
        now_week = getYearWeek(now);
    return now_week - start_week;
}

function range(a: number, b: number) {
    let res = [];
    for (let i = a; i <= b; i++) res.push(i);
    return res;
}

function concat(a: number[][]): number[] {
    return a.flat();
}

