function gen_table(week: number) {
    let classTableData: any[][] = [];

    // weeks
    for (let i = 0; i < d.courses.length; i++) {
        let temp: any[] = [];
        // init null
        for (let t = 0; t < max_course; t++) {
            temp.push(null);
        }
        // set courses
        for (let j of d.courses[i]) {
            if (j.weeks.includes(week)) {
                for (let p of j.period) {
                    p -= 1;
                    if (temp[p] !== null) {
                        console.log(
                            `初始化：冲突 in week:${week + 1}, period:${p + 1}\n old:${temp[p].course.name
                            }, new:${j.course.name}`
                        );
                    }
                    temp[p] = j;
                }
            }
        }
        classTableData.push(temp);
    }
    for (let i = d.courses.length; i < 7; i++) {
        let temp: any[] = [];
        for (let t = 0; t < max_course; t++) {
            temp.push(null);
        }
        classTableData.push(temp);
    }

    return classTableData;
}

window.onscroll = () => {
    let nav = document.getElementById("nav");
    if (nav) {
        if ((document.scrollingElement?.scrollTop || 0) > 200 || (document.scrollingElement?.scrollLeft || 0) > 200) {
            nav.className = "navbar navbar-expand-lg navbar-light bg-light sticky-top";
        } else {
            nav.className = "navbar navbar-expand-lg navbar-light bg-light";
        }
    }
};

