import { Component, Vue, Watch } from "vue-property-decorator";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

@Component
export default class Schedule extends Vue {
    merge = true;
    test = [
        "2022-01-10",
        "2022-01-11",
        "2022-01-12",
        "2022-01-13",
        "2022-01-14",
        "2022-01-17",
        "2022-01-18",
        "2022-01-19",
        "2022-01-20",
        "2022-01-21",
    ].map((s) => new Date(s));
    holiday = ["2021-09-21", "2021-10-01", "2022-04-05", "2022-06-03"].map(
        (s) => new Date(s)
    );
    weeks = [
        "monday",
        "tuesday",
        "wednesday",
        "thursday",
        "friday",
        "saturday",
        "sunday",
    ];
    week = now_week();

    @Watch("week")
    onWeekChanged(val: string, oldVal: any) {
        this.week = parseInt(val);
    }

    get classTableData() {
        return gen_table(this.week);
    }

    getDateByWeek(day: number) {
        const d = getDate(start, this.week, day);
        return `${d.getMonth() + 1}/${d.getDate()}`;
    }

    /**
     * 数字转中文
     * @param {number} num 需要转换的数字
     * @param {string} identifier 标识符
     * @returns {string} 转换后的中文
     */
    digital2Chinese(num: number, identifier: string) {
        const character = [
            "零",
            "一",
            "二",
            "三",
            "四",
            "五",
            "六",
            "七",
            "八",
            "九",
            "十",
            "十一",
            "十二",
        ];
        return identifier === "week" && (num === 0 || num === 7)
            ? "日"
            : character[num];
    }
}
